.box {
    margin: 50px;
}
.border {
    position: relative;
    width: 200px;
    height: 90px;
    line-height: 90px;
}
.line {
    background: #666;
    position: absolute;
}
.line-l {
    left: 0;
    width: 1px;
    height: 70px;
}
.line-t {
    width: 170px;
    height: 1px;
    top: 0;
    left: 0;
}
.line-r {
    right: 0;
    width: 1px;
    height: 70px;
    bottom: 0;
}
.line-b {
    width: 170px;
    height: 1px;
    bottom: 0;
    right: 0;
}
.lb {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 30px;
    height: 20px;
}
.lb1 {
    position: absolute;
    left: 0;
    top: 0;
    width: 1px;
    height: 9px;
    background: blue;
}
.lb2 {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 19px;
    height: 1px;
    -webkit-transform: translate(-2px, -5px) rotateZ(35deg);
            transform: translate(-2px, -5px) rotateZ(35deg);
    background: blue;
}
.lb3 {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 15px;
    height: 1px;
    background: blue;
}
.rt {
    position: absolute;
    right: 0;
    top: 0;
    width: 30px;
    height: 20px;
}
.rt1 {
    position: absolute;
    left: 0;
    top: 0;
    width: 15px;
    height: 1px;
    background: blue;
}
.rt2 {
    position: absolute;
    right: 0;
    bottom: top;
    width: 19px;
    height: 1px;
    -webkit-transform: translate(2px, 5px) rotateZ(35deg);
            transform: translate(2px, 5px) rotateZ(35deg);
    background: blue;
}
.rt3 {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 1px;
    height: 9px;
    background: blue;
}
.spot {
    width: 5px;
    height: 5px;
    border-radius: 50%;
    position: absolute;
    left: 20px;
    bottom: 0;
    background: red;
    -webkit-animation-name: run;
            animation-name: run;
    -webkit-animation-duration: 3s;
            animation-duration: 3s;
    -webkit-animation-delay: 2s;
            animation-delay: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}
@-webkit-keyframes run {
0%   {left:20px;
}
55%  {left:200px;bottom: 0;
}
100%  {bottom:75px; left: 200px;
}
}
@keyframes run {
0%   {left:20px;
}
55%  {left:200px;bottom: 0;
}
100%  {bottom:75px; left: 200px;
}
}

